*,
*:before,
*:after {
  box-sizing: border-box; }

html {
  font: 16px Roboto, Arial, Helvetica, sans-serif;
  font-display: swap; }

::selection {
  background: rgba(50, 50, 50, 0.3); }

/*
** Layout
*/
main,
section {
  min-height: 100vh; }

main,
section,
footer {
  padding: 25px;
  display: flex;
  align-items: center;
  justify-content: center; }

main {
  flex-direction: column; }

section {
  position: relative; }
  section:nth-of-type(odd) {
    z-index: 1;
    color: #fff; }
    section:nth-of-type(odd):before {
      content: '';
      position: absolute;
      z-index: -1;
      top: -5.2vh;
      right: 0;
      bottom: -5.2vh;
      left: 0;
      background-color: #607d8b;
      transform: skewY(-6deg); }
    section:nth-of-type(odd):first-of-type {
      margin-top: 0; }
    section:nth-of-type(odd):last-of-type {
      margin-bottom: 0; }
  section:nth-of-type(even) {
    margin: 10.4vh 0; }

footer {
  padding-top: 10.4vh;
  background-color: #424242;
  color: #fff; }

/*
** Type
*/
h1 {
  font-weight: 100;
  font-size: 3em; }

h2 {
  font-weight: 300;
  font-size: 1.5em; }

main h1,
main h2 {
  text-align: center; }

main h1 {
  margin: 5px 0;
  color: #607d8b;
  font-family: Raleway, Arial, Helvetica, sans-serif;
  font-weight: 300;
  letter-spacing: 2px;
  text-transform: uppercase;
  word-spacing: 2px; }

main h2 {
  color: #455a64; }

/*
** Profile Links
*/
.profile-links {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  margin-top: 25px; }
  .profile-links a {
    width: 128px;
    height: 128px;
    position: relative;
    margin: 10px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    border-radius: 50%;
    font: 15px 'Roboto', sans-serif;
    text-decoration: none; }
    .profile-links a, .profile-links a:after {
      transition: all 0.8s cubic-bezier(0.23, 1, 0.32, 1); }
    .profile-links a:after {
      content: '';
      position: absolute;
      top: 0;
      right: 0;
      bottom: 0;
      left: 0;
      z-index: -1;
      border-radius: 50%;
      transform: scale3d(1.15, 1.15, 1); }
    .profile-links a:hover {
      color: #fff;
      text-shadow: 0 1px 4px rgba(0, 0, 0, 0.15); }
      .profile-links a:hover:after {
        transform: scale3d(1, 1, 1); }
      .profile-links a:hover svg {
        filter: drop-shadow(0 1px 4px rgba(0, 0, 0, 0.15)); }
    .profile-links a svg {
      width: 48px;
      height: 48px;
      margin-bottom: 5px;
      fill: currentColor; }
  @media (min-width: 550px) {
    .profile-links {
      margin-top: 50px; }
      .profile-links a {
        font-size: 17px; } }

